import React, { Component } from "react";
import { Route, Redirect, Switch, Link, NavLink } from "react-router-dom";
import Kids from "./kids";
import Colddrinks from "./colddrinks";
import Hotdrinks from "./hotdrinks";
import Breakfast from "./breakfast";
import Lunch from "./lunch";
class Minimenu extends Component {
  //   state = {};
  selectpadding = e => {
    console.log(e.currentTarget.classList);
    console.log(e.currentTarget.innerHTML);
  };
  render() {
    // console.log(this.props.addtocart);
    return (
      <React.Fragment>
        <div className="col-lg-3">
          <div className="menu1-tab-menu">
            <ul className="nav nav-tabs">
              <li className="nav-item each-mem-container">
                <NavLink
                  className="nav-link d-flex  menu1-min justify-content-center "
                  to="/kids" onClick={(e)=>{this.selectpadding(e)}}             >
                  <div className="menu1-mem-icon">
                    <img src=".\IMG\dessert.png" alt="JSOFT" />
                  </div>
                  <h5>KIDS</h5>
                </NavLink>
              </li>
              <li className="nav-item each-mem-container">
                <NavLink
                  className="nav-link d-flex  menu1-min justify-content-center "
                  to="/coldrinks" onClick={(e)=>{this.selectpadding(e)}}
                >
                  <div className="menu1-mem-icon">
                    <img src=".\IMG\drink.png" alt="JSOFT" />
                  </div>
                  <h5>COLD DRINKS</h5>
                </NavLink>
              </li>
              <li className="nav-item each-mem-container">
                <NavLink
                  className="nav-link d-flex  menu1-min justify-content-center "
                  to="/hotdrinks" onClick={(e)=>{this.selectpadding(e)}}
                >
                  <div className="menu1-mem-icon">
                    <img src=".\IMG\810.jpg" alt="JSOFT" />
                  </div>
                  <h5>HOT DRINKS</h5>
                </NavLink>
              </li>
              <li className="nav-item each-mem-container">
                <NavLink
                  className="nav-link d-flex  menu1-min justify-content-center "
                  to="/breakfast" onClick={(e)=>{this.selectpadding(e)}}
                >
                  <div className="menu1-mem-icon">
                    <img src=".\IMG\vegs.png" alt="JSOFT" />
                  </div>
                  <h5>BREAKFAST</h5>
                </NavLink>
              </li>
              <li className="nav-item each-mem-container">
                <NavLink
                  className="nav-link d-flex  menu1-min justify-content-center "
                  to="/lunch" onClick={(e)=>{this.selectpadding(e)}}             >
                  <div className="menu1-mem-icon">
                    <img src=".\IMG\taco.png" alt="JSOFT" />
                  </div>
                  <h5>LUNCH</h5>
                </NavLink>
              </li>
            </ul>
          </div>
        </div>

        <div className="col-lg-5">
          <Switch>
            <Route exact path="/kids" >
              <Kids addTocart={this.props.addTocart} />
            </Route>
            <Route exact path="/" >
              <Kids addTocart={this.props.addTocart} />
            </Route>
            <Route exact path="/coldrinks">
              <Colddrinks adddrinkTocart={this.props.adddrinkTocart} />
            </Route>
            <Route exact path="/hotdrinks">
              <Hotdrinks adddrinkTocart={this.props.adddrinkTocart} />
            </Route>
            <Route exact path="/breakfast">
              <Breakfast addTocart={this.props.addTocart} />
            </Route>
            <Route exact path="/lunch">
              <Lunch addTocart={this.props.addTocart} />
            </Route>
          </Switch>
        </div>
      </React.Fragment>
    );
  }
}

export default Minimenu;
