import React, { Component } from 'react';
import Minimenu from './minimenu';
import { Route, Redirect,Switch,Link } from 'react-router-dom';
import Cart from './cart'
class Menu extends Component {
    state = {orders:[] }
    addTocart=(e)=>{
const name=e.currentTarget.children[0].children[0].innerHTML;
const price=e.currentTarget.children[0].children[1].innerHTML;
const clonedPrice=[...this.state.orders];
const id=Date.now().toString();
clonedPrice.push({name,price,id});
this.setState({orders:clonedPrice});
    }

    adddrinkTocart=(e)=>{
       const price=e.currentTarget.innerHTML.split(" ")[1];
        const name=e.currentTarget.parentElement.parentElement.children[0].innerHTML;
        const clonedPrice=[...this.state.orders];
const id=Date.now().toString();
clonedPrice.push({name,price,id});
this.setState({orders:clonedPrice});
    }
    render() { 

        return (<div
            className="row wrapper menu2-wrapper d-flex text-center justify-content-around pb-3"
            id="menu1">
      
            <h1 className=" col-12  mt-3 mb-5 ">Our Menu</h1>
            <Minimenu addTocart={this.addTocart} adddrinkTocart={this.adddrinkTocart}/>
            <Cart orders={this.state.orders} />
            </div>  )
    }
}
 
export default Menu;