import React,{Component} from 'react'; 
// import {useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import {withAlert} from 'react-alert'; 

import Axios from 'axios';
class Cart extends Component {
    state = {total:0  ,checker:0,orderid:0}
  
   
    convertor=()=>{
        this.props.orders.map((order)=>{
            let arr=order.price.split("");
            arr.splice(0,1);
             this.state.total= this.state.total+Math.ceil(Number(arr.join(""))); 
        })
    }
    saveorder=()=>{
        Axios.post("/order",{
            Orders:this.props.orders,
            total:this.state.total
        }).then(res=>{
       
        this.setState({orderid:res.data.id});
   
        })
    }
    render() { 
        
        console.log('fghj');
        if(this.props.orders.length==0)
        {  return(
            <div className="col-lg-3 cart-wrapper text-center">
            <h3 className="">Your Cart is Empty!! </h3>
            <h3 className=""> <i className="fa fa-spinner fa-spin"></i> </h3>
          
            </div>
        )}
        else 
        {
            if(this.state.orderid==0)
            {this.convertor();
                return ( <React.Fragment>
                <div className="col-lg-3 cart-wrapper text-center">
            
                {this.props.orders.map((order)=>{
                return <div className=" my-1 card cart-card" key={order.id}>
                    <div className="card-title text-left pl-3 pt-2">  {order.name}</div>
               <div className="card-body text-right pt-1 pb-2 pr-2">{order.price}</div>
               </div>
                })}
                <div className="card total my-2 ">
                  <h5>  Total: $ {this.state.total}</h5> 
            
            
                </div>
            <button  type="button" className="btn btn-md btn-outline-success my-3" onClick={this.saveorder} >Order! </button>      
            </div>
              
            </React.Fragment>
                 );
                
            }
            else
            {return(
                <div className="col-lg-3 cart-wrapper text-center">
                <h4 className="">Your order id is {this.state.orderid} </h4>
                
                <h3 className=""> <i className="fa fa-check fa-5x "></i> </h3>
              
                </div>
            )
        }
        }
      

    }
}

export default Cart;